import './main.css';
import React from 'react';
import ReactDOM from 'react-dom';
import App from './components/App.jsx';

$("#rollBtn").click(function() {
	// Allocate image resource
	var arbitraryResource = gapi.hangout.av.effects.createImageResource('data:image/png;base64,');

	// Create a statically positioned overlay that will be 50% of the width
	// of the video feed.
	var arbitraryOverlay = arbitraryResource.createOverlay(
	{'scale':
		{
			'magnitude': 0.5,
			'reference': gapi.hangout.av.effects.ScaleReference.WIDTH
		}
	});

	// Place the text x-centered and halfway down the frame
	arbitraryOverlay.setPosition(0, 0.25);
	arbitraryOverlay.setVisible(true);
});

function init() {
	// When API is ready...                                                         
	gapi.hangout.onApiReady.add(function(eventObj) {
		if (eventObj.isApiReady) {
			$("#rpgdice").removeClass("hidden");
		} else{
			$("#api-init-error").removeClass("hidden");
		}
	});
}

// Wait for gadget to load.                                                       
gadgets.util.registerOnLoadHandler(init);




ReactDOM.render(<App/>, document.getElementById('chat-pane'));



